<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block">
        <div class="btn-main mr15" @click="ClickDateLine">导出</div>
        <div @click="showDialog()" class="btn-main-deepen">考核规则</div>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="15%">班级</th>
          <th width="10%">姓名</th>
          <th width="15%">早自习到岗</th>
          <th width="15%">课间操跟跑</th>
          <th width="15%">晚自习到岗</th>
          <th width="15%">班主任例会</th>
          <th width="10%">总分</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item,key) in table.list">
          <td>{{item.className}}</td>
          <td>{{item.teacherName}}</td>
          <td>{{item.morningLessonNum}}</td>
          <td>{{item.breakExerciseNum}}</td>
          <td>{{item.eveningLessonNum}}</td>
          <td>{{item.regularMeetingNum}}</td>
          <td>{{item.totalAssessNum}}</td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
      <el-dialog title="考核规则" class="dialog" :visible.sync="dialog" width="50%">
        <resize-table>
        <table class="table">
          <thead>
          <tr>
            <th width="15%">年级</th>
            <th width="15%">早自习到岗周次数</th>
            <th width="15%">课间操跟跑周次数</th>
            <th width="15%">晚自习到岗周次数</th>
            <th width="15%">例会到岗周次数</th>
            <th width="10%">操作</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(roleItem, key) in roleList">
            <td>{{roleItem.gradeName}}</td>
            <td>
              <span v-if="roleItem.status!==true">{{roleItem.morningLessonTarget}}</span>
              <el-input
                      placeholder="请输入次数"
                      size="small"
                      v-show="true"
                      style="width: 90%"
                      v-if="roleItem.status===true"
                      v-model="form.morningLessonTarget"></el-input>
            </td>
            <td>
              <span v-if="roleItem.status!==true">{{roleItem.breakExerciseTarget}}</span>
              <el-input
                      placeholder="请输入次数"
                      size="small"
                      v-show="true"
                      style="width: 90%"
                      v-if="roleItem.status===true"
                      v-model="form.breakExerciseTarget"></el-input>
            </td>
            <td>
              <span v-if="roleItem.status!==true">{{roleItem.eveningLessonTarget}}</span>
              <el-input
                      placeholder="请输入次数"
                      size="small"
                      v-show="true"
                      style="width: 90%"
                      v-if="roleItem.status===true"
                      v-model="form.eveningLessonTarget"></el-input>
            </td>
            <td>
              <span v-if="roleItem.status!==true">{{roleItem.regularMeetingTarget}}</span>
              <el-input
                      placeholder="请输入次数"
                      size="small"
                      v-show="true"
                      style="width: 90%"
                      v-if="roleItem.status===true"
                      v-model="form.regularMeetingTarget"></el-input>
            </td>
            <td>
              <span class="oper"
                    @click="editRole(roleItem,key)"
                    v-if="roleItem.status!==true">编辑</span>
              <span class="oper"
                    @click="submitRole(roleItem,key)"
                    v-if="roleItem.status===true">完成</span>
              <span class="oper"
                    @click="cancelRole(roleItem,key)"
                    v-if="roleItem.status===true">取消</span>
            </td>
          </tr></tbody>
        </table>
        </resize-table>
      </el-dialog>
    </main-part>
  </div>
</template>
<script>
  import mixins from '../../../../assets/js/mixins'
  export default {
    mixins: [mixins.$exportDetail],
    data () {
      return {
        nav: [{name: '班主任助理考评', path: ''}],
        searchData: [
          {type: 'date', title: '月份', dateType: 'month', model: 'month',initialDate: new Date()},
          {type: 'select', title: '年级', model: 'gradeId', option: []}
        ],
        table: {
          url: '/advisorAssess/queryAdvisorAssistantAssessPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            month: new Date().$Format('yyyy-MM-dd hh:mm:ss'),
            gradeId: null,
            teacherPosition: 'HEADMASTER_ASSISTANT'
          }
        },
        // 考核规则
        roleList: [],
        dialog: false,
        form: {
          id: '',
          gradeId: '',
          morningLessonTarget: '',
          breakExerciseTarget: '',
          eveningLessonTarget: '',
          regularMeetingTarget: ''
        }
      }
    },
    methods: {
      // 处理时间--导出
      ClickDateLine(){
        let newDate = this.table.query.month
        this.table.query.month=this.table.query.month.substr(0,10)
        this.table.query.month=this.table.query.month.replace(/(\d{4})-(\d{2})-(\d{2})/g,'$1/$2/$3')
        this.$exportDetail('/export/teacherAssessExport.do')
        this.table.query.month = newDate
      },
      // 获取年级列表
      async getGradeList () {
        let res = await this.$ajax({
          url: '/grade/queryGradeList.do',
        })
        this.searchData[1].option = []
        res.forEach(e => {
          this.searchData[1].option.push({
            label: e.name,
            value: e.id
          })
        })
        this.table.query.gradeId = res[0].id
      },
      async showDialog () {
        this.dialog = true
      },
      // 获取考核
      async getRule () {
        this.roleList = await this.$ajax({
          url: '/advisorAssess/queryAssessInspectRule.do'
        })
      },
      // 展开编辑表单
      editRole (item, key) {
        this.form = {
          id: item.id,
          morningLessonTarget: item.morningLessonTarget,
          breakExerciseTarget: item.breakExerciseTarget,
          eveningLessonTarget: item.eveningLessonTarget,
          regularMeetingTarget: item.regularMeetingTarget
        }
        item.status = true
        this.roleList.splice(key, 1, item)
      },
      // 提交
      async submitRole (item, key) {
        await this.$ajax({
          url: '/advisorAssess/changeAssessInspectRule.do',
          query: this.form
        })
        item.status = false
        this.roleList.splice(key, 1, item)
        this.$remind('修改成功')
        await this.getRule()
      },
      // 取消
      cancelRole (item, key) {
        item.status = false
        this.roleList.splice(key, 1, item)
      }
    },
    async mounted () {
      await this.getGradeList()
      await this.getRule()
      this.$search(this.table)
    },
    watch: {
      dialog: function () {
        if (!this.dialog) {
          this.roleList.forEach(e => {e.status = false})
        }
      }
    }
  }
</script>
<style lang="sass" scoped>
</style>
